import Vue from 'vue'
/**
 * 
 */
function errorToShow(msg = '', callback = function() {}) {
	uni.showToast({
		title: msg,
		icon: 'none',
		duration: 1500
	})
	setTimeout(callback, 1500)
}
/**
 * 
 */
function successToShow(msg = '', callback = function() {}) {
	uni.showToast({
		title: msg,
		icon: 'success',
		duration: 1000
	})
	setTimeout(callback, 500)
}
/**
 * 
 */
function navigateTo(url, callback, events) {
	uni.navigateTo({
		url: url,
		animationType: 'pop-in',
		animationDuration: 300,
		success: callback,
		events: events
	})
}
/**
 * 
 */
