/**
 * (YYYY-HH-DD),
 */
function getDateByYear(year) {
	var dd = new Date();
	year && dd.setFullYear(dd.getFullYear() + year);
	var y = dd.getFullYear();
	var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1;
	var d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate();
	return y + '-' + m + '-' + d;
}
/**
 * 
 */
function formatDate(fmt = "yyyy-mm-dd", date) {
	if (date) {
		let arr = date.split(/[- : T /]/)
		arr[1] = arr[1] - 1
		date = new Date(...arr)
	} else {
		date = new Date()
	}
	let o = {
		"Y+": date.getFullYear(), //
		"M+": (date.getMonth() + 1), // 
		"D+": date.getDate(), // 
		"h+": date.getHours(), // 
		"m+": date.getMinutes(), // 
		"s+": date.getSeconds() // 
	}
	for (let k in o) {
		if (new RegExp("(" + k + ")").test(fmt)) {
			if (k != "Y+") {
				o[k] = String(o[k]).padStart(2, "0")
			}
			fmt = fmt.replace(RegExp.$1, o[k])
		}
	}
	return fmt
}
/**
 * 
 */
function getObjVal(obj, keys) {
	let newBbj = {}
	keys.forEach(key => {
		Object.assign(newBbj, {
			[key]: obj[key]
		})
	})
	return newBbj
}

export {
	errorToShow,
	successToShow,
	navigateTo,
	redirectTo,
	switchTabTo,
	navigateBack,
	reLaunch,
	getDateByYear,
	formatDate,
	getObjVal
}
